
import About from '@/components/Special/About.vue';
import Help from '@/components/Special/Help.vue';

import { defineComponent } from 'vue';

export default defineComponent({
    props: {
        view: String
    },
    components: { About, Help },

    methods: {
        popupHide() {
            this.$emit('hide');
        },

        popupClick(e: MouseEvent) {
            e.stopPropagation();
        }
    }
})
