
import store from '@/store/store';
import { defineComponent } from 'vue';

import SeriesDetails from '@/components/User/Details/SeriesDetails.vue';

export default defineComponent({
    props: {
        media: {}
    },

    components: { SeriesDetails },
    
    data() {
        return {
            state: store.state
        }
    },

    methods: {
        close(): void {
            this.$emit('close');
        }
    }
})
